import React from "react";
import style from  "./table.css";
import {Link} from "react-router-dom";
import  {productdetail5} from "./Productdetail5";
import  {productdetail7} from "./Productdetail7";
import  {productdetail8} from "./Productdetail8";

class productdetail6 extends React.Component{
    onAddtoCart = () => {
        this.props.history.push('/Cart')
      };
      render(){
     return(

     <div>
       <div className="small-container">
        <div className="row">
        <div className="col-2">
            <div className="food-pic">
            <img src="image/Products/pic6.png" alt="Mulemule Dabo"/></div></div>
            <div className="col-2">
            <div className= "food-details">
            <h2> Mulemule Dabo</h2>
            <p>Store /Food</p>
            <h3>$1.99</h3>
            <label for="quantity">Quantity:</label>
   <input type="number" id="quantity" name="quantity" min="1" max="50"/>
            <h4>Product Details</h4>
            <p>Mulmule dabo is a special bread made only for the celebration of Buha- Ethiopian holiday.</p><br/>
        
            <h3><button onClick={this.onAddtoCart} >Add to Cart</button></h3>
        </div>
        </div>
        </div>
    </div>
    <h2>You May also like</h2>
<div className=" small-container">
    <div className="row">
        <div className="col-4"><Link to="/productdetail5">
        <a> <img src="../assets/image/Products/pic4.png" alt="Defo Dabo"/></a></Link>
            <h2>Defo Dabo</h2>
            <h3>$7.00</h3>
            <button onClick={this.onAddtoCart} >Add to Cart</button></div>
            <div className="col-4"><Link to="/productdetail7">
            <a> <img src="../assets/image/Products/pic7.png" alt="Shero"/></a></Link>
                <h2> Shero</h2>
                <h3>$21.99</h3>
                <button onClick={this.onAddtoCart} >Add to Cart</button></div>
            <div class="col-4"><Link to="/productdetail8">
            <a> <img src="../assets/image/Products/pic8.jpg" alt="Kocho"/></a></Link>
                <h2> Kocho</h2>
                <h3>$15.50</h3>
                <button onClick={this.onAddtoCart} >Add to Cart</button></div>
        </div>
    </div>

           </div>
           );
 }   
}

export default productdetail6;