import React from "react";
import style from  "./table.css";
import {Link} from "react-router-dom";
import  {productdetail1} from "./Productdetail1";
import  {Productdetail2} from "./Productdetail2";
import  {Productdetail4} from "./Productdetail4";


class productdetail3 extends React.Component{
    onAddtoCart = () => {
        this.props.history.push('/Cart');
      }
    render(){
     return(

     <div>

        <div className="small-container">
        <div className="row">
            <div className="col-2">
            <div className="food-pic">
            <img src="../assets/image/Products/pic5.png" alt="Mitmita"/></div></div>
            <div className="col-2">
            <div className= "food-details">
            <h2> Mitmita</h2>
            <p>Store /Food</p>
            <h3>$16.50</h3>
            <label for="quantity">Quantity:</label>
   <input type="number" id="quantity" name="quantity" min="1" max="50"/>
            <h4>Product Details</h4>
            <p>Mitmita is red-pepper spice blend.</p><br/>
        
            <h3><button onClick={this.onAddtoCart} >Add to Cart</button></h3>
        </div>
        </div>
        </div>
    </div>
    
    <h2>You May also like</h2>
<div className=" small-container">
    <div className="row">
        <div class="col-4"><Link to ="/productdetail1">
        <a> <img src="../assets/image/Products/pic1.png" alt="Berbera"/></a></Link>
            <h2> Berbere</h2>
            <h3>$20.00</h3>
            <button onClick={this.onAddtoCart} >Add to Cart</button></div>
            <div className="col-4"><Link to ="/productdetail2">
            <a> <img src="../assets/image/Products/pic2.png" alt="Injera"/></a></Link>
                <h2>Injera</h2>
                <h3>$5.00</h3>
                <button onClick={this.onAddtoCart} >Add to Cart</button></div>

            <div className="col-4"><Link to ="/productdetail4">
            <a> <img src="../assets/image/Products/pic3.png" alt="Bula"/></a></Link>
                <h2> Bula</h2>
                <h3>$12.99</h3>
                <button onClick={this.onAddtoCart} >Add to Cart</button></div>
        </div>
    </div>

    </div>
 );
 }   
}

export default productdetail3;