import React from 'react';
import style from  './table.css';
import {Link} from "react-router-dom";
import Cart from './Cart';
import About from './About';
import product from './product';
import Account from './Account';
import contact from './contact';

class Nav extends React.Component{
   
  render(){
    return(

      <div>
            <div className="wrapper"> 
        <div className="navbar">
        <div className="logo">
               <img src="../assets/image/logo.jpg" width="200px" height="60px"/></div>
        
        <nav>
            <ul>
                <Link to="/About">
                    <li>About</li></Link>
                    <Link to="/product">
                    <li>Products</li></Link>
                    <Link to="/Account">
                    <li>Account</li></Link>
                    <Link to="/contact">
                    <li>Contact</li></Link>
            </ul>
            <div className="img"><Link to ="/Cart">
               <a> <img src="../assets/image/cart.png"  width="40px" height="40px"/></a></Link></div>
            
            <input type="text" placeholder="Search.."/>
        </nav>
    </div>
        </div>
        </div>
    
                 );
                }
              }
              
              
              export default Nav;